/*Se divide en dos secciones la pagina*/
.App {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "a b";
}

/*Se ocupa la primera seccion*/
.ContentDiv{
  grid-area: a;
  text-align: column;
}

/*Se ocupa la segunda seccion*/
.SocialsDiv{
  grid-area: b;
  text-align: column;
}

@media (max-width: 700px) {
  .App {
    display: grid;
    grid-template-columns: 2fr;
    grid-template-areas: 
    "a"
    "b";
  }
}

@media (max-width: 591px) {
  .App {
    display: grid;
    grid-template-columns: 2fr;
    grid-template-areas: 
    "a"
    "b";
  }

  img{
    left:33%;
    top:10;
  }
}

/*Wrapper de formato de la primera seccion*/
.Sec1-header {
  grid-area: a;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;

  position: relative;
}

/*Wrapper de formato de la segunda seccion*/
.Sec2-header {
  grid-area: b;
  background-color: #F3FFC6;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;

  position: relative;
}

.App-link {
  color: #61dafb;
}

